"use client";

import { Card } from "@/components/ui/card";
import { 
  CheckCircle2, 
  Clock, 
  AlertTriangle, 
  TrendingUp,
  Users,
  Calendar
} from "lucide-react";

interface StatsCardProps {
  title: string;
  value: number | string;
  type: "completed" | "inProgress" | "delayed" | "performance" | "team" | "upcoming";
  change?: number;
  subtitle?: string;
}

export default function StatsCard({ title, value, type, change, subtitle }: StatsCardProps) {
  const getIcon = () => {
    switch (type) {
      case "completed":
        return <CheckCircle2 className="h-8 w-8 text-green-500" />;
      case "inProgress":
        return <Clock className="h-8 w-8 text-blue-500" />;
      case "delayed":
        return <AlertTriangle className="h-8 w-8 text-red-500" />;
      case "performance":
        return <TrendingUp className="h-8 w-8 text-purple-500" />;
      case "team":
        return <Users className="h-8 w-8 text-indigo-500" />;
      case "upcoming":
        return <Calendar className="h-8 w-8 text-orange-500" />;
      default:
        return null;
    }
  };

  const getColorClass = () => {
    switch (type) {
      case "completed":
        return "bg-green-50";
      case "inProgress":
        return "bg-blue-50";
      case "delayed":
        return "bg-red-50";
      case "performance":
        return "bg-purple-50";
      case "team":
        return "bg-indigo-50";
      case "upcoming":
        return "bg-orange-50";
      default:
        return "bg-gray-50";
    }
  };

  const getChangeColor = (changeValue?: number) => {
    if (!changeValue) return "";
    return changeValue > 0 ? "text-green-600" : "text-red-600";
  };

  return (
    <Card className={`${getColorClass()} p-6 transition-all hover:shadow-lg`}>
      <div className="flex items-start justify-between">
        <div>
          <p className="text-sm font-medium text-gray-600">{title}</p>
          <div className="mt-2 flex items-baseline">
            <p className="text-2xl font-semibold text-gray-900">{value}</p>
            {change !== undefined && (
              <span
                className={`ml-2 text-sm font-medium ${getChangeColor(change)}`}
              >
                {change > 0 ? "+" : ""}
                {change}%
              </span>
            )}
          </div>
          {subtitle && (
            <p className="mt-1 text-sm text-gray-500">{subtitle}</p>
          )}
        </div>
        <div className="rounded-lg p-2">{getIcon()}</div>
      </div>

      {/* Progress bar for performance type */}
      {type === "performance" && typeof value === "number" && (
        <div className="mt-4">
          <div className="h-2 w-full bg-gray-200 rounded-full overflow-hidden">
            <div
              className="h-full bg-purple-500 rounded-full"
              style={{ width: `${value}%` }}
            />
          </div>
        </div>
      )}
    </Card>
  );
}

